import { Menu, MenuProps } from 'antd';
import React from 'react'
import { Link } from 'react-router-dom';

export const Header = () => {
  const menus = [{ label: '', path: '/' }, { label: 'Golang', path: '/' }, { label: 'OAuth', path: '/oauth' }]

  return <div>
    <div className='Logo'></div>
    <nav>
      <ul>
        {menus.map(item => <li><Link to={item.path}>{item.label}</Link></li>)}
      </ul>
    </nav>
  </div>

}
